.LoadingCircular {
    max-width: 180px;
    margin: auto;
    text-align: center;
    padding: 16px;
}

.cls-1{
    fill: none;
    stroke:#53c1de;
    stroke-miterlimit:10;
    stroke-dasharray: 185 185;
    stroke-dashoffset: 185;
    -webkit-animation: strokeAnim linear 1.7s infinite;
            animation: strokeAnim linear 1.7s infinite;
}

.cls-1:nth-child(2) { -webkit-animation-delay: 0.3s; animation-delay: 0.3s; }
.cls-1:nth-child(3) { -webkit-animation-delay: 0.6s; animation-delay: 0.6s; }

@-webkit-keyframes strokeAnim {
    0% { stroke-dashoffset: 185; }
    50% { stroke-dashoffset: 370; }
    100% { stroke-dashoffset: 555; }
}

@keyframes strokeAnim {
    0% { stroke-dashoffset: 185; }
    50% { stroke-dashoffset: 370; }
    100% { stroke-dashoffset: 555; }
}


